import store from '@/store';
import filters from '@/filters';
import i18n from '@/i18n';

let urlRegex = /([hH][tT][tT][pP]([sS]?):\/\/)?(\w+\.)+[A-Za-z]{2,}(\/\S+)?/gi;
let phoneRegex = /\d{8,256}/gi;

class Message {
  systemTypes = [
    'joinRoom',
    'leaveRoom',
    'addMember',
    'addMembers',
    'deleteMember',
    'recall'
  ];
  // 
  async textToUrlType(message) {
    if (message.messageType === 'text') {
      let urls = message.message.match(urlRegex);
      if (urls && urls.length > 0) {
        let originalUrl = [];
        let extra = [];
        let urlCache =
          JSON.parse(sessionStorage.getItem('IMKit-urlCache')) || [];
        for (let url of urls) {
          //  http://
          if (url.toLowerCase().indexOf('http') !== 0) {
            url = `http://${url}`;
          }
          // cache
          let urlData = null;
          let find = urlCache.find(item => {
            return item.url === url;
          });
          if (find) {
            urlData = find.data;
          } else {
            urlData = await store.state.api.url.getPreview(url);
            urlCache.push({
              url: url,
              data: urlData
            });
          }
          if (urlData) {
            originalUrl.push(url);
            extra.push(urlData);
          }
        }
        sessionStorage.setItem('IMKit-urlCache', JSON.stringify(urlCache));

        message.messageType = 'url';
        message.originalUrl = JSON.stringify(originalUrl);
        message.extra = JSON.stringify(extra);

        store.commit('checkScrollToBottom');
      }
    }
  }
  //  <a> 
  textView(message) {
    let _message = message
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/\n/g, '<br />');
    let messageList = [];
    let searchIdx = _message.search(urlRegex);

    while (searchIdx >= 0) {
      if (searchIdx > 0) {
        messageList.push(
          _message.substr(0, searchIdx).replace(phoneRegex, str => {
            return `<a href="tel:${str}" target="_blank">${str}</a>`;
          })
        );
        _message = _message.substr(searchIdx);
      }
      _message = _message.replace(urlRegex, str => {
        let link = str;
        if (link.indexOf('http') !== 0) {
          link = `http://${link}`;
        }
        messageList.push(`<a href="${link}" target="_blank">${str}</a>`);
        return '';
      });

      searchIdx = _message.search(urlRegex);
    }
    messageList.push(
      _message.replace(phoneRegex, str => {
        return `<a href="tel:${str}" target="_blank">${str}</a>`;
      })
    );
    return messageList.join('');
  }
  // 
  messagesTimespan(messages) {
    let isFirst = true;
    let timespan = '';
    for (let message of messages) {
      message.timespan = null;
      if (message.messageTimeMS) {
        let _timespan = filters.timeDiff(message.messageTimeMS, true);
        if (_timespan !== timespan) {
          message.timespan = _timespan;
          // 
          //  timespan
          if (_timespan === i18n.t('Today')) {
            if (isFirst) {
              message.timespan = null;
            }
            break;
          }
          timespan = _timespan;
          isFirst = false;
        }
      }
    }
  }
  addMembersMessage(message) {
    return i18n.t('{name} invites {members} to join.', {
      name: message.sender.nickname || i18n.t('Unknown Name'),
      members: JSON.parse(message.message)
        .map(member => {
          return member.nickname || i18n.t('Unknown Name');
        })
        .join(', ')
    });
  }
}

export default Message;



