import { mapActions } from 'vuex';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomListItem from '@/components/RoomList/RoomListItem/index.vue';

export default {
  name: 'RoomList',
  data() {
    return {
      searchText: '',
      list: null,
      isHideAddRoomBtn: true,
      hideAddRoomBtnTimer: null
    };
  },
  components: {
    RoomListItem,
    Scrollbar
  },
  computed: {
    listScroll() {
      return this.$refs.list;
    }
  },
  mounted() {
    this.hideAddRoomBtn();
  },
  methods: {
    filterSearch(room) {
      if (!this.searchText) {
        return true;
      } else {
        let searchText = this.searchText.toLowerCase();
        if (room.name && ~room.name.toLowerCase().indexOf(searchText)) {
          return true;
        } else if (room.members) {
          let find = room.members.find(member => {
            return (
              member.nickname &&
              ~member.nickname.toLowerCase().indexOf(searchText)
            );
          });
          if (find) {
            return true;
          }
        }
      }
      return false;
    },
    onListScroll() {
      if (
        !this.searchText &&
        this.$store.state.roomList &&
        !this.$store.state.roomListLoading && // 
        this.$store.state.roomListLoader && // 
        this.listScroll.scrollBottom() < 10
      ) {
        this.getRoomList();
      }
      //  add 
      this.hideAddRoomBtn();
    },
    hideAddRoomBtn() {
      if (this.hideAddRoomBtnTimer) {
        clearTimeout(this.hideAddRoomBtnTimer);
        this.hideAddRoomBtnTimer = null;
      }
      this.isHideAddRoomBtn = true;
      this.hideAddRoomBtnTimer = setTimeout(() => {
        this.isHideAddRoomBtn = false;
      }, 500);
    },
    notificationsOnClick() {
      this.$fcm
        .requestPermission()
        .then(() => {
          this.$fcm.getToken().then(refreshedToken => {
            if (this.$store.state.enableNotification) {
              // 
              this.unsubscribe(refreshedToken);
            } else {
              // 
              this.subscribe(refreshedToken);
            }
          });
        })
        .catch(error => {
          this.$log.error('[FCM requestPermission]', error);
        });
    },
    logout() {
      let logoutEvent = this.$store.state.config.list.logout.event;
      if (this.$store.state.enableNotification) {
        this.$fcm.getToken().then(refreshedToken => {
          this.unsubscribe(refreshedToken);
          if (logoutEvent) {
            logoutEvent();
          }
        });
      } else {
        if (logoutEvent) {
          logoutEvent();
        }
      }
    },
    roomClick(room) {
      if (room.id !== this.$store.state.roomActive.id) {
        this.setRoomActive(room.id);
      }
    },
    ...mapActions(['getRoomList', 'subscribe', 'unsubscribe', 'setRoomActive'])
  }
};



