import md5 from 'md5';
import store from '@/store';

class Room {
  //  key key 
  fullKeys(room) {
    let keys = ['name', 'cover', 'description', 'lastMessage'];
    for (let key of keys) {
      if (!(key in room)) {
        room[key] = null;
      }
    }
    room.unread = room.unread || 0;
    if (!room.roomType) {
      if (room.members.length !== 2) {
        room.roomType = 'group';
      } else {
        let hash = md5(
          room.members
            .map(m => {
              return m._id.toLowerCase();
            })
            .sort((a, b) => {
              return a > b ? 1 : -1;
            })
            .join('_')
        );
        if ((hash, hash === room._id)) {
          room.roomType = 'direct';
        } else {
          room.roomType = 'group';
        }
      }
    }

    // 
    if (store.state.config.special.renderRoomDescription) {
      room.description = store.state.config.special.renderRoomDescription(
        room.description
      );
    }
  }
}

export default Room;



