import moment from 'moment';
import store from '@/store';
import i18n from '@/i18n';
import lib from '@/lib';

export default {
  roomDisplayName: room => {
    if (room.name) {
      return room.name;
    } else if (!room.members) {
      return ' ';
    } else {
      let membersNickname = room.members
        .filter(member => {
          return member.id !== store.state.client.client;
        })
        .map(member => {
          return member.nickname || i18n.t('Unknown Name');
        });
      if (membersNickname.length === 0) {
        return i18n.t('No member');
      } else {
        return membersNickname.join(', ');
      }
    }
  },
  roomDisplayDescription: room => {
    if (!room.description) {
      return '';
    } else {
      return room.description;
    }
  },
  roomDisplayCover: room => {
    if (room.cover) {
      return room.cover;
    } else if (!room.members || room.members.length <= 1) {
      return '';
    } else {
      let sliceMembers = room.members
        .filter(member => {
          return member.avatarUrl && member.id !== store.state.client.client;
        })
        .slice(0, 4);
      let avatars = sliceMembers.map(member => {
        return member.avatarUrl;
      });
      if (avatars.length === 0) {
        return '';
      } else if (avatars.length === 1) {
        room.cover = avatars[0];
      } else {
        room.cover = avatars;
      }
      return room.cover;
    }
  },
  messageFormat: (message, showSender = true) => {
    let getSenderName = () => {
      return showSender
        ? message.sender.nickname || i18n.t('Unknown Name')
        : '';
    };
    let getMemberName = () => {
      return message.member && message.member.nickname
        ? message.member.nickname
        : i18n.t('Unknown Name');
    };
    switch (message.messageType) {
      case 'url':
      case 'text':
        return message.message;
      case 'addMember':
      case 'joinRoom':
        return i18n.t('{name} joined room.', {
          name: getMemberName()
        });
      case 'addMembers':
        return lib.message.addMembersMessage(message);
      case 'leaveRoom':
        return i18n.t('{name} leave room.', {
          name: getMemberName()
        });
      case 'deleteMember':
        return i18n.t('{sender} has removed {name}.', {
          sender: getSenderName(),
          name: getMemberName()
        });
      case 'sticker':
        return i18n.t('{name} send sticker.', {
          name: getSenderName()
        });
      case 'image':
        return i18n.t('{name} send image.', {
          name: getSenderName()
        });
      case 'video':
        return i18n.t('{name} send video.', {
          name: getSenderName()
        });
      case 'audio':
        return i18n.t('{name} send audio.', {
          name: getSenderName()
        });
      case 'file':
        return i18n.t('{name} send file.', {
          name: getSenderName()
        });
      case 'location':
        return i18n.t('{name} send location.', {
          name: getSenderName()
        });
      case 'recall':
        return i18n.t('{name} recall a message.', {
          name: getSenderName()
        });
      case 'encrypted':
        return i18n.t('{name} send an encrypted message.', {
          name: getSenderName()
        });
      case 'request_payment':
        return i18n.t('{name} send a payment request.', {
          name: getSenderName()
        });
      case 'payment':
        return i18n.t('{name} send a payment.', {
          name: getSenderName()
        });
      default:
        return i18n.t('Unsupported message.', {
          name: getSenderName()
        });
    }
  },
  timeDiff: (date, option = false) => {
    const tmp = moment(date);

    if (moment().isSame(tmp, 'day')) {
      return option ? i18n.t('Today') : tmp.format('HH:mm');
    } else if (tmp.isSame(moment().subtract(1, 'days'), 'day')) {
      return i18n.t('Yesterday');
    } else if (moment().isSame(tmp, 'week')) {
      let dayStr = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
      ][tmp.day()];
      return i18n.t(dayStr);
    } else if (moment().isSame(tmp, 'year')) {
      return tmp.format('M/D');
    } else {
      return tmp.format('YYYY/M/D');
    }
  },
  dateFormat: (date, format) => {
    return moment(date).format(format);
  }
};



